import { useEffect, useState } from "react";

import haversineDistance from "haversine-distance";
import { DisplayHillList } from "./displayHill";
import { loadHillsDatabase } from "./hillsDatabase";
import { Coordinate, Hill } from "./models";
import Select from "react-select";
import { hillOptions } from "./models";

export const NearestHill = () => {
  const [hillsList, setHillsList] = useState<Hill[]>([]);
  const [nearestHills, setNearestHills] = useState<NeighbouringHill[]>();
  const [classification, setClassification] = useState<string>("Tu");

  useEffect(() => {
    loadHillsDatabase().then((data) => {
      setHillsList(data);
    });
  }, []);

  const updateNearestHill = (coordinate: Coordinate) => {
    setNearestHills(
      getNearestNeighbours(coordinate, hillsList, classification)
    );
  };

  const updateDisplayData = () => {
    console.log("Location retreived...");
    const location = getLocation();
    location
      .then((result) => updateNearestHill(result))
      .then(() => console.log(NearestHill));
  };

  const handleChange = (selectedOption: any) => {
    console.log("Selection Change...");
    setClassification(selectedOption.value);
  };

  const customStyles = {
    option: (provided: any) => ({
      ...provided,
      color: 'black'
    }),
    control: (provided: any) => ({
      ...provided,
      color: 'black'
    }),
    singleValue: (provided: any) => ({
      ...provided,
      color: 'black'
    })
  }

  return (
    <div>
      <div>
        <Select
          options={hillOptions}
          defaultValue={hillOptions[0]}
          onChange={handleChange}
          autoFocus={true}
          styles = {customStyles}
        />
      </div>
      <button onClick={() => updateDisplayData()}>
        Find your nearest summit
      </button>
      {nearestHills ? <DisplayHillList hills={nearestHills} /> : ""}
    </div>
  );
};

export interface NeighbouringHill {
  hill: Hill;
  distance: number;
}

function getNearestNeighbours(
  coordinate: Coordinate,
  hills: Array<Hill>,
  classification: string,
  nNeighbours: number = 5
): Array<NeighbouringHill> {
  type SortPosition = {
    idx: number;
    distance: number;
  };

  const hillsOfClassification = hills.filter((hill) => {
    return Object(hill)[classification] === "1";
  });

  const distances: Array<SortPosition> = hillsOfClassification.map(
    (hill, index) => {
      const distance = haversineDistance(coordinate, {
        latitude: hill.Latitude,
        longitude: hill.Longitude,
      });
      return { idx: index, distance: distance };
    }
  );

  const sortedDistances = distances.sort(function (a, b) {
    return a.distance - b.distance;
  });

  const closestPositions = sortedDistances.slice(0, nNeighbours);
  const nearestHills = closestPositions.map(
    (position) => hillsOfClassification[position.idx]
  );

  let result = [];
  for (let i = 0; i < nearestHills.length; i++) {
    result.push({
      hill: nearestHills[i],
      distance: closestPositions[i].distance,
    });
  }
  return result;
}

function getLocation() {
  return new Promise<Coordinate>(function (resolve, reject) {
    navigator.geolocation.getCurrentPosition(function (pos) {
      const lat = pos.coords.latitude;
      const lon = pos.coords.longitude;
      resolve({ latitude: lat, longitude: lon });
    }, () => console.log("Unable to get GPS"),
    { enableHighAccuracy: true});
  });
}
